header {
  width: 100%;
  position: absolute;
  left: 0;
  z-index: 2;
}
header .container {
  min-height: 97px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
header .container::after {
  content: "";
  height: 1px;
  width: calc(100% - 30px);
  background-color: #a2a2a2;
  position: absolute;
  bottom: 0;
  left: 15px;
}
header .logo img {
  height: 30px;
}
header nav {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
header nav ul {
  display: flex;
}
@media (max-width: 767px) {
  header nav ul {
    display: none;
  }
  @media (max-width: 767px) {
    header nav ul {
      width: 100%;
      height: 590px;
      background-color: rgb(0 0 0 / 100%);
      position: absolute;
      right: 0;
      top: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      border-radius: 30px;
    }
  }
  header nav .toggle-menu:hover + ul {
    width: 100%;
    background-color: rgb(0 0 0 / 50%);
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 100%;
    left: 0;
  }
  header nav ul li a {
    padding: 15px;
  }
}
header nav ul li a {
  color: white;
  padding: 42px 20px;
  display: block;
  text-decoration: none;
  font-size: 12px;
  transition: 0.3s;
  position: relative;
  z-index: 2;
}
@media (max-width: 767px) {
  header nav ul li a {
    padding: 30px 20px;
    font-size: 14px;
  }
}
@media (min-width: 768px) {
  header nav ul li a.active {
    color: var(--main-color);
    border-bottom: 1px solid var(--main-color);
  }
}
header .container .icons {
  width: 100px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  header .container .icons {
    flex: 1;
    justify-content: flex-end;
  }
}
header .container .icons i {
  width: 30px;
  height: 30px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
header .container .icons i.xmark,
header .container .icons i.bars {
  display: none;
  font-size: 16px;
}
@media (max-width: 767px) {
  header .container .icons i.bars {
    display: flex;
  }
}
