.footer {
  padding-top: calc(var(--section-padding) / 2);
  padding-bottom: calc(var(--section-padding) / 2);
  background-image: url("../../Assets/subscribe.jpg");
  background-size: cover;
  position: relative;
  color: white;
  text-align: center;
}
.footer::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0 0 0 / 90%);
}
.footer .container {
  position: relative;
}
.footer img {
  margin-bottom: 20px;
}
.footer p:not(.copyright) {
  text-transform: uppercase;
  padding: 20px;
  border-bottom: 1px solid white;
  font-size: 20px;
  width: fit-content;
  margin: 10px auto;
}
.footer .social-icons a {
  color: rgb(209, 204, 204);
  padding: 10px 15px;
  display: inline-block;
}
.footer .copyright {
  margin-top: 30px;
  font-size: 13px;
}
.footer .copyright span:not(.year) {
  font-weight: bold;
  color: var(--main-color);
}
