.services {
  padding-top: var(--section-padding);
  padding-bottom: var(--section-padding);
}
@media (min-width: 768px) {
  .services .services-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
    gap: 60px;
  }
}
.services .srv-box {
  display: flex;
}
.services .srv-box i {
  margin-right: 20px;
}
@media (max-width: 767px) {
  .services .srv-box {
    display: flex;
    margin-bottom: 40px;
    flex-direction: column;
    text-align: center;
  }
  .services .srv-box i {
    margin: 0 0 30px;
  }
}
.services .srv-box h3 {
  color: var(--main-color);
  margin-bottom: 20px;
  font-size: 14px;
}
.services .srv-box p {
  color: #777;
  line-height: 2;
  font-size: 10px;
}
