.landing {
  min-height: 100vh;
  background-color: #1f2021;
  background-image: url("../../Assets/landing.jpg");
  background-size: cover;
  position: relative;
}
.landing .overlay {
  width: 100%;
  height: 100%;
  background-color: rgb(0 0 0 / 80%);
  position: absolute;
  left: 0;
  top: 0;
}
.landing .text {
  color: white;
  padding: 50px;
  background-color: var(--transparent-color);
  width: 50%;
  display: flex;
  justify-content: flex-end;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.landing .text .content {
  max-width: 500px;
}
@media (max-width: 767px) {
  .landing .text {
    width: 100%;
  }
  .landing .text .content {
    max-width: 100%;
  }
}
.landing .text .content h2 {
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: normal;
  line-height: 1.5;
}
.landing .text .content p {
  font-size: 11px;
  line-height: 2;
}
